.title {
  background-color: rgba(0, 0, 0, 0.1);
  -webkit-background-clip: text;
  -moz-background-clip: text;
  /* font-family: 'Cutive', sans-serif; */
  background-clip: text;
  text-shadow: rgba(255, 255, 255, 0.2) 0px 3px 3px;
  font-size: 12vw;
  min-height: 22vh;

  display: flex;

  /* justify-content: center; */
}

.banner {
  /* font-size: calc(5px + 1.5vw); */
  font-size: 2vw;

  animation: fadeInAnimation 1s;
  font-family: "Libre Franklin", sans-serif;
}

.project-link{
  height:15vh;
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
}




.btn {
  line-height: 50px;
  height: 50px;
  border-radius: 50%;
  text-align: center;
  width: 50px;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
}

.btn-projects {
  color: rgba(0, 0, 0, 0.5);
  text-shadow: rgba(255, 255, 255, 0.2) 0px 3px 3px;
 
  position: relative;
}

.btn-projects:hover{
  box-shadow:inset 0 0 10px  rgba(0, 0, 0, 0.3); 

  
}


.btn-projects:active{
  background-color: rgba(0, 0, 0, 0.3);
}
.btn-projects::before {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;


  z-index: 1;
  opacity: 0;
  transition: all 0.3s;

  transform: scale(0.1, 1);
}
.btn-projects:hover span {
  display: none;
}
.btn-projects:hover::before {
  opacity: 1; 
  transform: scale(1, 1); 
}
.btn-projects::after {

  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  border-radius: 50%;
  height: 100%;
  z-index: 1;
  /* transition: all 0.1s; */


  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19)
  
}
.btn-projects:hover::after {
  opacity: 0; 
  transform: scale(0.1, 1);

}

.contact{
  width: 10%;
  justify-content: flex-start;
  align-items: flex-start;
}


@media only screen and (max-width: 1000px) {
  .layout-home {
    flex-direction: column;
    row-gap: 20vw;
    justify-content: center;
    
  }
  .title {
  
    /* min-width: 100%; */
    min-height: 15vw;
   
  }
  .banner {
    font-size: 4vw;
  }

  .btn-projects{
    width:15vw;
    height:15vw;
    position: relative;
  }

  .avatar {
    max-width: 50vw;
  }

  .contact-mobile{
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .contact-desktop{
    display: none;
  }

  .project-link{
   height: 10vh;
  }
  .content{
    min-height: 40vh;
  }

}
